@import url(https://fonts.googleapis.com/css2?family=Saira+Stencil+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Saira&display=swap);
#root {
  width: 100%;
  height: 100%;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: Saira;
  }
body, html {    
  width: 100%;
  height: 100%;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.App {
  width: 100%;
  height: 100%;
}

.top-panel {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;

  padding: 4px 16px;

  background-color: rgba(105, 105, 105, 1);
}
.top-panel-text {
  width: 1124px;
  padding: 0px 10px;
  
  font-size: 24px;
  font-family: Saira Stencil One;
  color: rgba(232, 232, 232, 1);
}

.bottom-panel {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;

  background-color: rgba(105, 105, 105, 1);
}
.bottom-panel-text {
  display: flex;
  justify-content: flex-end;

  width: 1124px;

  font-family: Saira;
  font-size: 20px;
  color: rgba(255, 255, 255, 1);
}
.bottom-panel-text span {
  color: #CBCACA;
}
.main {
    margin: 0 auto;
    display: flex; 
    max-width: 1124px; 
    min-height: 100%;
}
.panel {
    max-width: 500px;  
    padding: 10px 0 0 10px; 
}

.form {
    width: 100%;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    padding-bottom: 16px;
    padding-right: 22px;

    border-bottom: 1px solid rgba(196, 196, 196, 1);
}

.form input {
    flex: 1 1;

    border: none;
    outline: none;

    padding: 5px 16px;
    background: #E8E8E8;
    border-radius: 5px;

    margin-bottom: 10px;
}
.form input:nth-child(odd) {
    margin-right: 11px;
  }

.form textarea {
    width: 100%;
    Height: 75px;

    resize: none;

    border: none;
    outline: none;
    padding: 5px 16px;
    background: #E8E8E8;
    border-radius: 5px;
}

.form-btn-box {
    display: flex;
    width: 100%;
    margin-top: 10px;
}
.form-btn {
    width: 100%;
    background: #696969;
    border-radius: 5px;
    padding: 4px 22px;
    color: white;
    border: none;

    cursor: pointer;
}
.form-btn:last-child {
    margin-left: 12px;
}



/* animation - loading list */
.isFetching {
    width: 100px;
    height: 100px;
    border-radius: 100%;
    margin: 10vh auto;
    border: 2px dashed red;

    -webkit-animation-name: seach-fetching;

            animation-name: seach-fetching;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

@-webkit-keyframes seach-fetching {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg) scale(1.3);
                transform: rotate(360deg) scale(1.3);
    }
}

@keyframes seach-fetching {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg) scale(1.3);
                transform: rotate(360deg) scale(1.3);
    }
}

/* Modal window about not unique ID */
.modal {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    border: 1px solid rgba(105, 105, 105, 1);
    border-radius: 10px;
    background: rgba(232, 232, 232, 0.7);
    
    text-align: center;
    padding: 15px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.bike-list {
    width: 100%;
    height: 100%;
    border-right: 1px solid rgba(196, 196, 196, 1);
    padding: 9px 9px 0 12px;
}

select {
    border: none;
    outline: none;
    background: #E8E8E8;
}

.bikes-wrapper {
    display: flex;
    background: rgb(232, 232, 232);
    border-radius: 10px;
    margin-bottom: 12px;
    padding: 12px 12px 12px 18px;
    max-width: 100%;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
}
.available {
    border: 2px solid rgba(111, 207, 151, 1);
}
.busy {
    border: 2px solid rgba(242, 153, 74, 1);
}
.unavailable {
    border:  2px solid rgba(172, 86, 86, 0.5);
}

.unavailable .bike-wrapper-left > *:not( .bikes-wrapper-status ) {
    opacity: 0.5;
}
.unavailable .bike-wrapper-right {
    opacity: 0.5;
}

.bike-wrapper-left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.bike-wrapper-right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.bikes-wrapper-name {
    font-size: 14px;
}
.bikes-wrapper-slug {
    font-size: 8px;
    line-height: 13px;
}
.bikes-wrapper-status select {
    cursor: pointer;
}
.bikes-wrapper-price {
    font-size: 24px;
}

.bnt {
    width: 20px;
    height: 20px;

    cursor: pointer;
}



.statistic {
    padding-top: 10px;

}
.statistic * {
    padding-bottom: 5px;
}

.statistic-header {
    font-weight: bold;
    font-size: 18px;
}
